import * as React from 'react';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Grid from '@mui/material/Grid2';
import Typography from '@mui/material/Typography';
import {styled} from '@mui/material/styles';
import {Container} from "@mui/material";
import {Link} from 'react-router-dom';

const cardData = [
    {
        redirect: '/gallery',
        tag: 'Gallery',
        title: 'Infinity Nikki ',
        description: 'Browse through my favorite Infinity Nikki photos, capturing the games beauty and fashion',
    },
    {
        img: 'https://picsum.photos/800/450?random=2',
        redirect: 'https://mewtru.com/valentine',
        tag: 'Special Message',
        title: 'A Personal Note Just for You',
        description: 'I\'ve crafted a special message just for you. Click to here read it',
    },
]

const StyledCard = styled(Card)(({theme}) => ({
    display: 'flex',
    flexDirection: 'column',
    padding: 0,
    height: '100%',
    backgroundColor: (theme.vars || theme).palette.background.paper,
    '&:hover': {
        animation: "myAnim 1s ease-in-out 0s 1 normal forwards",
        outline: '3px solid',
        outlineColor: 'hsla(210, 98%, 48%, 0.5)',
        outlineOffset: '2px',
        backgroundColor: 'transparent',
        cursor: 'pointer',
    }
}));

const StyledCardContent = styled(CardContent)({
    display: 'flex', flexDirection: 'column', gap: 4, padding: 16, flexGrow: 1, '&:last-child': {
        paddingBottom: 16,
    },
});

const StyledTypography = styled(Typography)({
    display: '-webkit-box',
    WebkitBoxOrient: 'vertical',
    WebkitLineClamp: 2,
    overflow: 'hidden',
    textOverflow: 'ellipsis',
});

export default function MainContent() {

    return (<Container
        maxWidth="lg"
        component="main"
        sx={{display: 'flex', flexDirection: 'column', my: 16, gap: 4}}
    >
        <Box sx={{display: 'flex', flexDirection: 'column', gap: 4}}>
            <div>
                <Typography>"We dreamed alone, but together we believed</Typography>
            </div>
            <Grid container spacing={2} columns={12}>
                {cardData.map((card) => (
                    <Grid size={{xs: 12, md: 4}}>
                        <Link to={card.redirect} style={{textDecoration: 'none'}} target={card.redirect.startsWith("http") ? "_blank" : "_self"} rel="noreferrer">
                            <StyledCard
                                variant="outlined"
                                tabIndex={0}
                                sx={{height: '100%'}}
                            >
                                <StyledCardContent
                                    sx={{
                                        display: 'flex',
                                        flexDirection: 'column',
                                        justifyContent: 'space-between',
                                        height: '100%',
                                    }}
                                >
                                    <div>
                                        <Typography gutterBottom variant="caption" component="div">
                                            {card.tag}
                                        </Typography>
                                        <Typography gutterBottom variant="h6" component="div">
                                            {card.title}
                                        </Typography>
                                        <StyledTypography
                                            variant="body2"
                                            color="text.secondary"
                                            gutterBottom
                                        >
                                            {card.description}
                                        </StyledTypography>
                                    </div>
                                </StyledCardContent>
                            </StyledCard>
                        </Link>
                    </Grid>
                ))}
            </Grid>
        </Box>
    </Container>);
}